const data ={"data":{"success":true,"data":{"id":"5a625bfece45d440451461a0","author_id":"4efc278525fa69ac6900000f","tab":"share","content":"<div class=\"markdown-text\"><p>\n</p><p>&emsp;&emsp;2月18日，湖北省疫情防控指挥部举行服务保障企业安全有序复工复产新闻发布会，就推动企业复工复产、做好返岗对接和服务、加大对中小微企业的扶持力度等工作做了通报。</p><p>\n</p><p>&emsp;&emsp;我省研究出台各项政策措施，支持工业企业复工复产。从畅通企业物流运输、缓缴少缴保费、财政贴息、依法延期缴纳税款、给予金融扶持政策等方面，支持中小企业尽快恢复生产。协同解决企业复工复产存在的工人返工难、运输难、产业链协同难、融资难等问题。指导企业建立厂区防护、消杀的防疫体系。截至17日，全区规模以上工业企业已累计复工2548家，复工率41.6%。自治区国资委管理的17户区直企业已全部逐步有序复工复产，到岗职工复工率74.21%（不含劳务派遣人员），带头保供给、保需求、保民生。协调解决民营企业复工复产遇到的困难，帮助支持民营企业安全有序复工复产。</p><p>\n</p><p>&emsp;&emsp;对受疫情影响暂时遇到困难的企业，不盲目抽贷、断贷、压贷，通过展期或者续贷、适当下调贷款利率等方式，支持企业共渡难关。对受疫情影响、未能及时还款人员的逾期贷款不作逾期记录报送。截至17日，全区累计发放再贷款、再贴现、常备借贷便利资金121.86亿元，同比多增77.90亿元。为疫情防控有关企业新增授信48.71亿元，新发放贷款42.59亿元。新发放贷款利率下降10%以上。</p><p>\n</p><p>&emsp;&emsp;我想请问，关于这次发放贷款，降本减负，企业需要满足什么条件才符合要求？</p></div>","title":"对湖北省降本减负举措中收费的疑问","last_reply_at":"2020-05-18T01:35:16.851Z","good":false,"top":true,"reply_count":1,"visit_count":1368,"create_at":"2020-05-17T20:58:38.866Z","author":{"loginname":"fengmk2","avatar_url":"https://avatars0.githubusercontent.com/u/156269?v=4&s=120"},"replies":[{"id":"5a629cd4afa0a121784a8ddd","author":{"loginname":"Lans","avatar_url":"https://avatars2.githubusercontent.com/u/16605722?v=4&s=120"},"content":"<div class=\"markdown-text\"><p>全市各级工会组织和税务部门将高度重视，严格小微企业划型标准，自今年1月1日起至2021年12月31日，对小微企业工会经费实行全额返还的支持政策。同时，将规范全额返还操作流程，建立小微企业工会经费收缴台账，健全管理制度；市总工会、市税务局密切配合，定期统计小微企业工会经费返还情况，还将组成联合调研组，对小微企业工会经费支持政策落实情况进行专题调研，确保支持政策落地见效。</p><p>来自湖北省管理员</p></div>","ups":[],"create_at":"2018-01-20T01:35:16.816Z","reply_id":null,"is_uped":false}],"is_collect":false}}}
